								title: 'æ æ³çæååæµ·æ¥ï¼'
							});
							return
						}
					}, 1000);
					uni.downloadFile({
						url: this.imgTop,
						success: (res) => {
							arrImagesUrlTop = res.tempFilePath;
							let arrImages = [this.posterbackgd, arrImagesUrlTop, this.PromotionCode];
