								this.$util.activityCanvas(arrImages, this.storeCombination.title,
									this.storeCombination.price, this.storeCombination.people +
									'äººå¢', 'è¿å·®' + this.count + 'äººæ¼å¢æå', 9,
									(tempFilePath) => {
										this.imagePath = tempFilePath;
										this.canvasStatus = true;
										uni.hideLoading();
									});
							}, 500);
						}
					});
				}
