			// çæäºç»´ç ï¼
			make() {
				let href = location.protocol + '//' + location.host +
					'/pages/activity/goods_combination_status/index?id=' + this.pinkId + "&spread=" + this.uid;
				uQRCode.make({
					canvasId: 'qrcode',
